import React, {useState,useEffect} from 'react'
import { Switch, Link, useHistory} from 'react-router-dom'
import '../styles/navbar.css'
import {useAuth} from '../auth/AuthProvider'
import { supabase } from '../supabase'


function NavBar() {
    const [isLogged, setIsLogged] = useState()
    const {signOut} = useAuth()
    const history = useHistory()

    async function handleLogout() {
        await signOut()
        history.push('/login')
    }
    useEffect(()=>{
        const user = supabase.auth.user()
        setIsLogged(user)
    },[])

    return (
        <Switch>
            <div className="navigation">
            <div className="left-nav">
                <Link id="nav-option" to="/app-gallery">Home</Link>
                <Link id="nav-option" to="/games">Games</Link>
                <Link id="nav-option" to="/upload">Upload</Link>
                <Link id="nav-option" to="/discussions">Discussions</Link>
            </div>
            {isLogged ?  <div className="right-nav">
                            <Link id="nav-option" to="/account">Account</Link>
                            <Link id="nav-option" onClick={handleLogout} to="/login">Log out</Link>
                        </div> : 
                        <div className="right-nav">
                            <Link id="nav-option" to="/login">Login</Link>
                            <Link id="nav-option" to="/register">Register</Link>
                        </div>
                        }
        </div>
        </Switch>
        
    )
}
export default NavBar
